import Component from 'flarum/common/Component';
import icon from 'flarum/common/helpers/icon';
export default class SubscriptionMenuItem extends Component {
  view() {
    return m("button", {
      className: "SubscriptionMenuItem hasIcon",
      onclick: this.attrs.onclick
    }, this.attrs.active && icon('fas fa-check', {
      className: 'Button-icon'
    }), m("span", {
      className: "SubscriptionMenuItem-label"
    }, icon(this.attrs.icon, {
      className: 'Button-icon'
    }), m("strong", null, this.attrs.label), m("span", {
      className: "SubscriptionMenuItem-description"
    }, this.attrs.description)));
  }
}
flarum.reg.add('flarum-subscriptions', 'forum/components/SubscriptionMenuItem', SubscriptionMenuItem)